@import url(https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Bai Jamjuree", sans-serif;
}
h1 {
  font-size: 45px;
}
h2,
h3 {
  font-weight: bold;
}
p {
  color: hsl(201, 11%, 66%);
  font-size: 18px;
}

.Topimg {
  background-image: url(/Clipboard-Landing-WebApp/static/media/bg-header-desktop.3d39ff8e.png);
  background-repeat: no-repeat;
  background-size: 100% 15%;
  text-align: center;
}

@media (max-width: 1028px) {
  .Topimg {
    background-image: url(/Clipboard-Landing-WebApp/static/media/bg-header-mobile.611a5565.png);
    background-repeat: no-repeat;
    background-size: 100% 15%;
  }
}

.Intro {
  display: flex;
  justify-content: center;
}
.Intro div {
  margin: 0 50px;
}
.Intro h1 {
  font-size: 40px;
}
.Intro h2 {
  margin-top: 125px;
}
.Intro p {
  margin: 0 100px;
}

.Logo {
  margin: 150px auto 25px;
}
@media (min-width: 260px) and (max-width: 860px) {
  .Intro p {
    margin: 0px;
  }
}

.Desktop {
  display: flex;
  margin: 75px auto;
  text-align: left;
}
.Desktop h3 {
  font-size: 25px;
}
.Desktop img {
  position: relative;
  margin: 25px auto;
  left: -50px;
  width: 100%;
}
.Snippets {
  margin-top: 50px;
  margin-left: 50px;
}

@media (max-width: 1028px) {
  .Desktop {
    display: inline-block;
    text-align: center;
  }
  .Desktop img {
    position: static;
    width: 75%;
  }
  .Snippets {
    margin: 50px 25px;
  }
}

.Access {
  display: flex;
  justify-content: center;
  margin: 125px auto;
}
.Access img {
  margin-top: 100px;
  width: 65%;
}

@media (max-width: 1028px) {
  .Access {
    margin: 75px 25px;
  }
  .Access img {
    margin-top: 75px;
    width: 100%;
  }
}

.Workflow {
  display: flex;
  justify-content: center;
  margin: 0 25px;
}

.WorkflowDescriptionLayout {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 50px auto;
}
.WorkflowDescription {
  width: 50%;
  margin: 25px;
}
.WorkflowDescription img {
  width: 50px;
}

@media (min-width: 260px) and (max-width: 860px) {
  .WorkflowDescriptionLayout {
    flex-direction: column;
  }
  .WorkflowDescription {
    width: 75%;
  }
}

.TestimonialsLayout {
  display: flex;
  justify-content: space-around;
  margin: 50px 15px;
}

.Testimonials img {
  margin: 25px;
}

@media (min-width: 260px) and (max-width: 1025px) {
  .TestimonialsLayout {
    display: flex;
    flex-direction: column;
  }
}

.Downloads {
  margin: 125px 25px 75px;
}

.Footer {
  background-color: hsl(0, 0%, 89%);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 50px;
  text-align: left;
}
.Footer img {
  width: 75px;
  height: 75px;
}
.Footer p {
  color: black;
  cursor: pointer;
  font-size: 16px;
  margin: 15px;
}
.Footer p:hover {
  color: hsl(171, 66%, 44%);
}
.LogoImg img {
  width: 25px;
  height: 25px;
  margin: 5px;
}
.LogoImg img:hover {
  cursor: pointer;
  -webkit-filter: invert(56%) sepia(99%) saturate(332%) hue-rotate(121deg)
    brightness(90%) contrast(88%);
          filter: invert(56%) sepia(99%) saturate(332%) hue-rotate(121deg)
    brightness(90%) contrast(88%);
}

@media (max-width: 1028px) {
  .Footer {
    display: block;
    flex-direction: column;
    text-align: center;
  }
}

