.Desktop {
  display: flex;
  margin: 75px auto;
  text-align: left;
}
.Desktop h3 {
  font-size: 25px;
}
.Desktop img {
  position: relative;
  margin: 25px auto;
  left: -50px;
  width: 100%;
}
.Snippets {
  margin-top: 50px;
  margin-left: 50px;
}

@media (max-width: 1028px) {
  .Desktop {
    display: inline-block;
    text-align: center;
  }
  .Desktop img {
    position: static;
    width: 75%;
  }
  .Snippets {
    margin: 50px 25px;
  }
}
