.Workflow {
  display: flex;
  justify-content: center;
  margin: 0 25px;
}

.WorkflowDescriptionLayout {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 50px auto;
}
.WorkflowDescription {
  width: 50%;
  margin: 25px;
}
.WorkflowDescription img {
  width: 50px;
}

@media (min-width: 260px) and (max-width: 860px) {
  .WorkflowDescriptionLayout {
    flex-direction: column;
  }
  .WorkflowDescription {
    width: 75%;
  }
}
