.Footer {
  background-color: hsl(0, 0%, 89%);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 50px;
  text-align: left;
}
.Footer img {
  width: 75px;
  height: 75px;
}
.Footer p {
  color: black;
  cursor: pointer;
  font-size: 16px;
  margin: 15px;
}
.Footer p:hover {
  color: hsl(171, 66%, 44%);
}
.LogoImg img {
  width: 25px;
  height: 25px;
  margin: 5px;
}
.LogoImg img:hover {
  cursor: pointer;
  filter: invert(56%) sepia(99%) saturate(332%) hue-rotate(121deg)
    brightness(90%) contrast(88%);
}

@media (max-width: 1028px) {
  .Footer {
    display: block;
    flex-direction: column;
    text-align: center;
  }
}
